.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



.App-header {
  background-image: url("https://moewalls.com/wp-content/uploads/2022/07/anime-school-girl-watching-the-sunset-thumb.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media (max-width: 510px) {
  .App-header {
    background-position: -992px;
  }
}

.App-link {
  color: #61dafb;
}


